@import (less) "color.less";

@md-width: 21.875em;
@sm-width: 14.5em;
@lg-width: 31.25em; 
@min-heigth: 3.125em;

.top,.bottom, .right, .top-right, .bottom-right, .top-left, .bottom-left{
  .icon-position {
    position:absolute;
    top: 1.3125em;
    right: 1.3125em;
    color: #fa9600;
    font-weight:bold;
  }
}

.text-padding-click {
    padding: 20px 80px 20px 20px;
    text-align: left;
    font-size: 14px;
    font-family: Arial;
}
.text-padding-hover {
    padding: .5em 1em .5em 1em;
}

.left{
  .icon-position {
    position:absolute;
    top: .3125em;
    left: .3125em;
    color: #fa9600;
    font-weight:bold;
  }
}

.nbp-tooltip, .nbp-popover {
    position: relative;
    display: inline-block;
    opacity: 0.9;
    z-index: 1000;
}

.clickable{
  cursor: pointer;
}


/* Tooltip text */

.nbp-tooltip .tooltip-content , .nbp-popover .popover-content{
    visibility: hidden;
    background-color: @darkest_gray;
    color: #fff; 
    padding: 0.3125em 0; 
    position: absolute;
    z-index: 1;
    display: inline-block !important; 
    .tooltip-text{
      text-align: center;
      font-weight: normal;
    }
    
    .popover-text{
      text-align: center;
      font-weight: normal;
    }

    &.md{
      width: @md-width;
    }
    &.sm{
      width: @sm-width;
    }
    &.lg{
      width: @lg-width;
    }
}

.nbp-tooltip .tooltip-content.right, .nbp-popover .popover-content.right {
    top: 50% !important;
    left: 100% !important;
    margin-left:0.625em;
    transform: translateY(-50%);
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 100%;
        transform: translateY(-50%); 
        border-top-width: 0.625em;
        border-right-width: 0.5625em;
        border-bottom-width: 0.625em;
        border-left-width: 0.5625em;
        border-style: solid;
        border-color: transparent @darkest_gray transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.left , .nbp-popover .popover-content.left {
    top: 50%;
    right: 100%;
    margin-right:0.625em;
    transform: translateY(-50%);
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translateY(-50%);
        border-top-width: 0.625em;
        border-right-width: 0.5625em;
        border-bottom-width: 0.625em;
        border-left-width: 0.5625em;
        border-style: solid;
        border-color: transparent transparent transparent @darkest_gray;
    }
}

.nbp-tooltip .tooltip-content.top , .nbp-popover .popover-content.top {
    bottom: 100%;
    left: 62%;
    margin-bottom:0.8125em;
    transform: translateX(-50%); 
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%; 
        transform: translateX(-50%);
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom , .nbp-popover .popover-content.bottom {
    top: 100%;
    left: 50%;
    margin-top:0.9375em;
    transform: translateX(-50%); 
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        left: 50%;
        transform: translateX(-50%); 
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip .tooltip-content.top-left , .nbp-popover .popover-content.top-left {
    bottom: 100%; 
    left: -4px;
    margin-bottom: 0.9375em;
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 5%; 
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.top-right , .nbp-popover .popover-content.top-right {
    bottom: 100%; 
    right: -14px;
    margin-bottom:0.9375em; 
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        right: 5%; 
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom-left , .nbp-popover .popover-content.bottom-left {
    top: 100%;
    left: -4px;
    margin-top:0.9375em;
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        left: 5%;
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom-right , .nbp-popover .popover-content.bottom-right {
    top: 100%;
    right: -14px;
    margin-top:0.9375em;
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        right: 5%;
        border-top-width: 0.625em;
        border-right-width: 0.75em;
        border-bottom-width: 0.625em;
        border-left-width: 0.75em;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip:hover .tooltip-content , .nbp-popover .popover-content.opened {
    visibility: visible;
}

:host-context(.contrasto-on) {
    .top,.bottom, .right, .top-right, .bottom-right, .top-left, .bottom-left{
        .icon-position {
            color: @white;
        }
    }
    .left{
        .icon-position {
            color: @white;
        }
    }

    /* Tooltip text */

    .nbp-tooltip .tooltip-content , .nbp-popover .popover-content{
        background-color: @black;
    }

    .nbp-tooltip .tooltip-content.right, .nbp-popover .popover-content.right {
        &::after {
            border-color: transparent @black transparent transparent;
        }
    }

    .nbp-tooltip .tooltip-content.left , .nbp-popover .popover-content.left {
        &::after {
            border-color: transparent transparent transparent @black;
        }
    }

    .nbp-tooltip .tooltip-content.top , .nbp-popover .popover-content.top {
        &::after {
            border-color: @black transparent transparent transparent;
        }
    }

    .nbp-tooltip .tooltip-content.bottom , .nbp-popover .popover-content.bottom {
        &::after {
            border-color: transparent transparent @black transparent;
        }
    }

    .nbp-tooltip .tooltip-content.top-left , .nbp-popover .popover-content.top-left {
        &::after {
            border-color: @black transparent transparent transparent;
        }
    }

    .nbp-tooltip .tooltip-content.top-right , .nbp-popover .popover-content.top-right {
        &::after {
            border-color: @black transparent transparent transparent;
        }
    }

    .nbp-tooltip .tooltip-content.bottom-left , .nbp-popover .popover-content.bottom-left {
        &::after {
            border-color: transparent transparent @black transparent;
        }
    }

    .nbp-tooltip .tooltip-content.bottom-right , .nbp-popover .popover-content.bottom-right {
        &::after {
            border-color: transparent transparent @black transparent;
        }
    }
}